import React from "react"
import { Link } from "gatsby"
import styled from "styled-components"

import posed from "react-pose"

import "../styles/global.css"

const StyledUlPosed = posed.div({
    closed: {justifyContent: 'space-between', height: '40px', transition: { duration: 500 }},
    open: {justifyContent: 'space-between', height: '100%', transition: { duration: 0 }, delayChildren: 300, staggerChildren: 50}
});

const LiPosed = posed.li({
    closed: {
        applyAtEnd: { visibility: 'hidden'},
        opacity: 0,
     },
    open: {
        applyAtStart: { visibility: 'visible' },
        opacity: 1
    },
});

const StyledUlMobile = styled(StyledUlPosed)`
    position: fixed;
    display: flex;
    flex-direction: column;
    list-style: none;
    margin: 0px 0;
    line-height: 40px;
    z-index: 998;
    background-color: #E9AFB8;
    width: 100%;
    transition-duration: 1s;
    padding: 5px 0px 3px 0px;

    li:nth-child(5) {
        margin-bottom: 100px;
    }
`

const StyledUlDesktop = styled.ul`
    position: fixed;
    display: flex;
    flex-direction: row;
    height: auto;
    list-style: none;
    margin: 0px 0px;
    line-height: 40px;
    z-index: 998;
    background-color: #E9AFB8;
    width: 100%;
    transition-duration: 1s;
    align-items: center;
    padding: 10px 0px;

    li {
        margin-right: 30px;
    }
`


const StyledLink = styled(Link)`
    text-decoration: none;
    font-family: 'Druk-Wide-Medium';
    color: #0A5640;
    font-size: 26px;
    margin-left: 20px;

    @media (min-width: 321px) {
        font-size: 30px;
      }

    @media (min-width: 992px) {
        font-size: 22px;
    }
`;

          class MenuTop extends React.Component {
              constructor(props) {
                  super(props)
                  this.state = {
                    showMenu: false,
                    showMobileMenu: false,
                    windowSize: null
                  }

                  this.onClick = this.onClick.bind(this);
                  this.onClickSecond = this.onClickSecond.bind(this);

              }

              componentDidMount(){

                        if(this.props.location.pathname !== '/') {
                            return this.setState( prevState => ({
                                showMenu: !prevState.showMenu
                            }) );
                        };

              }

              onClick(){

                    document.querySelector('.menutop').style.transform = 'translateY(-100%)';

                    this.setState(prevState => ({
                        showMobileMenu: false
                      }))
              }

              onClickSecond(){
                  this.setState(prevState => ({
                    showMobileMenu: !prevState.showMobileMenu
                  }))

                setTimeout(function(){

                },1000);
              }

            
          render() {

            if(this.props.windowSize < 992) {
                return(
                    <StyledUlMobile pose={this.state.showMobileMenu ? 'open' : 'closed'} className={this.state.showMobileMenu ? 'menutop' : 'menutop'} style={{transform: 'translateY(0%)'}}>
                    <li><StyledLink onClick={this.onClick}  style={{flexGrow: 2, fontFamily: 'DrukCond-Super', fontSize: '40px'}} to="/" >GEMEINDEHAUS</StyledLink><span className={'mobile-menu-burger'} onClick={this.onClickSecond}>MENU</span></li>
                    <LiPosed onClick={this.onClickSecond}><StyledLink activeStyle={{textDecoration: 'underline'}}   to="/workspaces" state={{from: this.props.location.pathname }}>WORKSPACES</StyledLink></LiPosed>
                    <LiPosed onClick={this.onClickSecond}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/about" state={{from: this.props.location.pathname }}>ABOUT</StyledLink></LiPosed>
                    <LiPosed onClick={this.onClickSecond}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/stpauli" state={{from: this.props.location.pathname }}>ST. PAULI</StyledLink></LiPosed>
                    <LiPosed onClick={this.onClickSecond}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/kontakt" state={{from: this.props.location.pathname }}>KONTAKT</StyledLink></LiPosed>
                    </StyledUlMobile>  
                )
            } else {
                return(
                    <StyledUlDesktop pose={this.state.showMobileMenu ? 'open' : 'closed'} className={this.state.showMobileMenu ? 'menutop' : 'menutop'} style={{transform: 'translateY(0%)'}}>
                    <li style={{flexGrow: 1, fontSize: '45px'}}><StyledLink onClick={this.onClick}  style={{fontFamily: 'DrukCond-Super', fontSize: 'inherit'}} to="/" >GEMEINDEHAUS</StyledLink><span className={'mobile-menu-burger'} onClick={this.onClickSecond}>MENU</span></li>
                    <li onClick={this.onClickSecond}><StyledLink activeStyle={{textDecoration: 'underline'}}   to="/workspaces" state={{from: this.props.location.pathname }}>WORKSPACES</StyledLink></li>
                    <li onClick={this.onClickSecond}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/about" state={{from: this.props.location.pathname }}>ABOUT</StyledLink></li>
                    <li onClick={this.onClickSecond}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/stpauli" state={{from: this.props.location.pathname }}>ST. PAULI</StyledLink></li>
                    <li onClick={this.onClickSecond}><StyledLink activeStyle={{textDecoration: 'underline'}}  to="/kontakt" state={{from: this.props.location.pathname }}>KONTAKT</StyledLink></li>
                    </StyledUlDesktop>  
                )
            }
        }
      }


export default MenuTop
