import React from "react"
import posed, { PoseGroup } from "react-pose"
import MenuTop from "../components/menutop"


const timeout = 0

class Transition extends React.Component {
  constructor(){
    super();
    this.state={

    }
  }

  updateDimensions() {

    this.setState({
      windowSize: window.innerWidth
    })
    
  }

  componentDidMount() {

    this.setState({
      windowSize: window.innerWidth
    })

    this.updateDimensions();
    window.addEventListener("resize", this.updateDimensions.bind(this));

  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateDimensions.bind(this));
  }

  render() {
    const { children, location } = this.props

    const RoutesContainer = posed.div({
      enter: { delay: timeout, delayChildren: timeout},
    })


    // To enable page transitions on mount / initial load,
    // use the prop `animateOnMount={true}` on `PoseGroup`.
    return (
        <>
    <MenuTop windowSize={this.state.windowSize} location={location}/>
      <PoseGroup style={{backgroundColor: '#0A5640'}} className='overflowScroll' location={location} preEnterPose='preEnter'>
        <RoutesContainer key={location.pathname}>    
            {children}
        </RoutesContainer>
      </PoseGroup>
      </>
    )
  }
}

export default Transition;